<template>

  <div class="row max-height-vertical box-overflow overflow-hidden">
    
    <div class="span-8-lg relative ind-2">
      <div class="overflow-image" container-card="overflow-card" :style="ratio_styles">
        <image-component v-bind:image="image"></image-component>
      </div>
    </div>

    <div class="span-8-lg bg-white relative ind-1 ">
      <div class="scroll-outer">
        <div class="slide-content relative ind-1">
          <slot></slot>
        </div>

        <!-- <crosshair-background></crosshair-background> -->
        <!-- <span-background></span-background> -->
      </div>
    </div>

  </div>
  
</template>

<script>
  import image_component from '../atoms/image_component'
  import span_background from '../atoms/span_background'
  import crosshair_background from '../atoms/crosshair_background'

  import {ratio_height, ratio_styles} from '../../utils/ratio_height'

  export default {
    props: ['image'],
    components: {
      'image-component': image_component,
      'crosshair-background': crosshair_background,
      'span-background': span_background
    },
    data () {
      return {
        ratio_styles
      }
    },
    mounted () {
      ratio_height(this, '[container-card="overflow-card"]', 2/3)
      window.addEventListener('resize', () => {
        ratio_height(this, '[container-card="overflow-card"]', 2/3)
      })
    }
  }
</script>

<style scoped lang="scss">

  @import '../../styles/variables';

  // vertical align without flexbox
  // see http://zerosixthree.se/vertical-align-anything-with-just-3-lines-of-css/

  .overflow-image {
    position: relative;
    width: 100%;
    padding-right: calc(100%/8);
    top: 50%;
    transform: translateY(-50%);
    @media screen and (max-width: $md-max) {
      box-sizing: border-box;
      padding-right: 0px;
      margin-bottom: 72px;
    }
    @media screen and (max-width: $sm-max) {
      margin-bottom: 0px;
    }
  }

  .slide-outer {
    display: flex;
  }


  .slide-content {
    position: relative;
    margin: auto;
    padding-top: 24px;
    padding-bottom: 24px;
    max-width: calc((100% * 6/8) - 48px); // 48px to account for margin
    &.extra-max-width {
      @media screen and (max-width: $md-max) {
        max-width: calc(100% - 24px - 24px);
      }
    }
  }
</style>



